.containerView {


    display: flex;
    width: 100%;
    border-top: 1px solid;
    border-color: chartreuse;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;

    .tittleImage {
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            width: 500px;
            height: 400px;
            border-radius: 50px;

        }
    }



    h3 {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2rem;
        margin-bottom: 25px;
        margin-top: 10px;
        border-color: $c1;
        font-family: 'augustus';

    }

    .btnAgregar {
        width: 300px;
        height: 50px;
        color: white;
        background-color: rgba(128, 255, 0, 0.461);
        border: 0px;
        border-radius: 50px;
        -webkit-box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.2);


        &:enabled {

            background-color: rgb(61, 112, 10);

            &:hover {
                background-color: rgba(128, 255, 0, 0.816);
            }
        }


    }

    .termComp {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 250px;
        height: 35px;
        color: white;
        background-color: $c1;
        border: 0px;
        border-radius: 50px;
        -webkit-box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.2);
        text-decoration: blink;
        text-transform: none;

    }

}

.description {
    display: flex;
    width: 100%;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    margin-left: 100px;
    margin-right: 100px;


    h4 {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 25%;
        height: 50px;
        font-size: 2rem;
        margin-bottom: 25px;
        border-bottom: solid 3px;
        border-color: $c1;


    }

    p {
        display: flex;
        text-align: justify;
        width: 100%;
        width: fit-content;
        font-size: 1.25rem;
        font-family: 'cherry';
        margin-left: 100px;
        margin-right: 100px;


    }


}



.btnCtrlCant {
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    margin-bottom: 25px;

    .btnSumar {
        border: 0px;
        border-radius: 10px;
        background-color: rgb(139, 90, 245);
        margin-right: 15px;

        &:hover {
            border-color: rgb(139, 90, 245);
            background-color: rgb(216, 208, 234);

        }

        &:focus {
            background-color: green;
        }

    }

    .btnRestar {
        border: 0px;
        border-radius: 10px;
        background-color: rgb(139, 90, 245);
        margin-left: 15px;

        &:hover {
            border-color: rgb(139, 90, 245);
            background-color: rgb(216, 208, 234);

        }

        &:focus {
            background-color: red;
        }

    }


}






@media (max-width: 1200px) {
    .containerView {
        display: flex;
        width: 100%;
        border-top: 1px solid;
        border-color: chartreuse;
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: center;


        .tittleImage img {

            margin-left: 0px;
        }

        .description {
            display: flex;
            width: 80%;
            flex-flow: row wrap;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            margin-top: 100px;
            margin-left: 100px;
            margin-right: 100px;
        }
    }
}

