@media (max-width: 500px) {
    .containerView {

        .tittleImage {
            padding-left: 10px;
            padding-right: 10px;

            img {
                width: 100%;
            }
        }


    }
}



@media (max-width: 350px) {
    .containerView {

        .tittleImage img {
            width: 250px;
            height: fit-content;
        }

    }
}